import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import { Provider } from 'react-redux';
import store from './store/store';
import { UserIsAuthenticated, UserIsNotAuthenticated } from './auth/auth';

// Components
import Navbar from './components/layouts/Navbar';
import Login from './components/auth/Login';
import Register from './components/auth/Register';
import AddPost from './components/admin/AddPost';
import Settings from './components/admin/Settings';
import PostsList from './components/admin/PostsList';
import EditPost from './components/admin/EditPost';
import Homepage from './components/pages/Homepage';
import Single from './components/pages/Single';

// Main Style
import './App.css';

class App extends Component {
  render() {
    return (
      <Provider store={store}>
        <Router>
          <div className="App">
            <Navbar branding="Bloggy" />
            <div className="container">
              <Switch>
                <Route exact path="/" component={Homepage} />
                <Route exact path="/admin/posts" component={UserIsAuthenticated(PostsList)} />
                <Route exact path="/post/:id" component={Single} />
                <Route exact path="/admin/posts/add" component={UserIsAuthenticated(AddPost)} />
                <Route exact path="/admin/post/edit/:id" component={UserIsAuthenticated(EditPost)} />
                <Route exact path="/admin/settings" component={UserIsAuthenticated(Settings)} />
                <Route exact path="/login" component={UserIsNotAuthenticated(Login)} />
                <Route exact path="/register" component={UserIsNotAuthenticated(Register)} />
              </Switch>
            </div>
          </div>
        </Router>
      </Provider>
    );
  }
}

export default App;
