import React, { Component } from 'react';
import AdminNavbar from '../admin/AdminNavbar';
import Input from '../helpers/Input';
import { storage } from '../../store/store';
import { firestoreConnect } from 'react-redux-firebase';

class AddPost extends Component {
  constructor(){
    super();
    this.state = {
      title: '',
      details: '',
      image: null,
      url: '',
      progress: 0,
      currentCategory: '',
      categories: [],
      wasSubmitted: false,
    }
  }

  onChange = e => {
    this.setState({
      [e.target.name]: e.target.value,
    })
  }

  addCategoriesHandler = e => {
    const { currentCategory, categories } = this.state;
    if(currentCategory !== '' && categories.indexOf(currentCategory) === -1){
      categories.push(currentCategory);
      this.setState({categories, currentCategory: ''})
    } else {
      // ERROR MESSAGE
    }
  }

  dismissCategory = (i) => {
    const { categories } = this.state;
    categories.splice(i, 1);
    this.setState({
      categories,
    })
  }

  selectFileHandler = e => {
    const image = e.target.files[0];
    if(image.type.split('/')[0] === 'image'){
      this.setState({
        image,
      });
      const uploadTask = storage.ref(`images/${image.name}`).put(image);
      uploadTask.on('state_changed', 
        (snapshot) => {
          const progress = Math.round((snapshot.bytesTransferred / snapshot.totalBytes) * 100) + '%';
          this.setState({
            progress,
          });
        },
        (err) => {
          console.error(Error(err));
        },
        () => {
          storage.ref('images').child(image.name).getDownloadURL().then(url => {
            this.setState({
              url,
            });
          });
        }
      );
    } else {
      // ERROR MESSAGE
    }
  }

  onSubmit = e => {
    e.preventDefault();
    const { wasSubmitted } = this.state;
    if(!wasSubmitted){
      this.setState({
        wasSubmitted: true,
      });
      const { title, details, categories, url } = this.state;
      const { firestore, history } = this.props;
      const newPost = {title, details, categories, url, created_at: Date.now()};
      firestore.add({collection: 'posts'}, newPost).then(() => {
        history.push('/admin/posts');
      }, err => {
        // ERROR MESSAGE
      })
    }
    return false;
  }

  render(){
    const { title, details, progress, image, url, currentCategory, categories } = this.state;
    return (
      <React.Fragment>
        <AdminNavbar />
        <div className="container">
          <div className="post-form">
            <div className="text-center my-2">
              <h2 className="heading-secondary">Add New Post</h2>
            </div>
            <div className="form">
              <form onSubmit={this.onSubmit}>
                <Input type="text" name="title" id="title" onChange={this.onChange} value={title} placeholder="Add Post Title" label="Add Post Title" />
                <div className="form__group">
                  <textarea name="details" value={details} onChange={this.onChange} className="form__input" placeholder="Add Post Details" rows="10"></textarea>
                  <label htmlFor="details" className="form__label">Add Post Details</label>
                </div>
                <div className="form__group form__cat-group">
                  <input type="text" name="currentCategory" id="currentCategory" className="form__input form__input-inline" onChange={this.onChange} placeholder="Add Categories" value={currentCategory} />
                  <button type="button" className="btn btn--secondary btn-sm btn-inline ml-1" onClick={e => {
                    e.preventDefault();
                    this.addCategoriesHandler();
                    }}>Add</button>
                  <label htmlFor="currentCategory" className="form__label">Add Categories</label>
                  <div className="form__categories-holder">
                    <ul className="chips">
                      {categories.map((category, i) => {
                        return (
                          <li key={category} onClick={this.dismissCategory.bind(this, i)} className="chip">{category} <i className="fas fa-times ml-1"></i></li>
                        )
                      })}
                    </ul>
                  </div>
                </div>
                <div className="form__group file-field my-4">
                  <div className="file-field__wrapper">
                    <span>{ image ? image.name : 'Upload Post Thumbnail' }</span>
                    <button className="btn btn--secondary btn-sm file-btn">Choose Image <i className="fas fa-cloud-upload-alt"></i></button>
                  </div>
                  <input type="file" name="thumbnail" id="thumbnail" onChange={this.selectFileHandler} />
                  {progress ? (
                    <React.Fragment>
                    <style dangerouslySetInnerHTML={{
                      __html: [
                        '.progress::before{',
                        `content: "${progress}";`,
                        `left: ${parseInt(progress) - 4}%;`,
                        '}',
                      ].join('\n')
                    }}></style>
                    <div className="progress progress--file" style={{width:progress, backgroundColor: progress === '100%' ? '#12d183' : '#05f'}}></div>
                    </React.Fragment>
                  ) : null}
                </div>
                <div className="img-thumbnail">
                  {url !== '' ? (
                    <img src={url} alt="Thumnail"/>
                  ) : null}
                </div>
                <button type="submit" className="btn btn--primary btn-block">Publish Post<i className="fas fa-location-arrow"></i></button>
              </form>
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default firestoreConnect()(AddPost);