import React, { Component } from 'react';
import { NavLink, Link } from 'react-router-dom';
import { compose } from 'redux';
import { connect } from 'react-redux';
import { firebaseConnect } from 'react-redux-firebase';
import PropTypes from 'prop-types'

class Navbar extends Component {
  constructor(){
    super();
    this.state = {
      auth: false,
    }
  }

  static getDerivedStateFromProps(props){
    const { auth } = props;
    if(auth.uid){
      return {
        auth: true,
      }
    } else {
      return {
        auth: false,
      }
    }
  }

  logOut = e => {
    e.preventDefault();
    const { firebase } = this.props;
    firebase.logout();
  }

  render(){
    const { branding } = this.props;
    const { auth } = this.state;
    return (
      <header className="header">
        <nav className="navbar">
          <div className="container">
            <Link to="/" className="navbar__logo">{branding}</Link>
            <ul className="navbar__links-auth">
              {auth ? (
                <React.Fragment>
                  <li className="navbar__item">
                    <a href="#!" onClick={this.logOut} className="navbar__link">Logout</a>
                    <i className="fas fa-sign-out-alt navbar__logout-icon"></i>
                  </li>
                  <li className="navbar__item">
                    <NavLink href="#!"  className="navbar__link" activeClassName="active" to="/admin/posts">Admin Area</NavLink>
                  </li>
                </React.Fragment>
              ) : (
                <React.Fragment>
                  <li className="navbar__item">
                    <NavLink exact activeClassName="active" to="/login" className="navbar__link">Login</NavLink>
                  </li>
                  <li className="navbar__item">
                    <NavLink exact to="/register" className="navbar__link" activeClassName="active">Register</NavLink>
                  </li>
                </React.Fragment>
              )}
            </ul>
          </div>
        </nav>
      </header>
    );
  }
}

Navbar.propTypes = {
  firebase: PropTypes.object.isRequired,
  auth: PropTypes.object.isRequired,
}

export default compose(
  firebaseConnect(),
  connect(state => {
    return {
      auth: state.firebase.auth,
    }
  })
)(Navbar);