import { createStore, combineReducers, compose } from 'redux';
import firebase from 'firebase';
import 'firebase/firestore';
import { reactReduxFirebase, firebaseReducer } from 'react-redux-firebase';
import { reduxFirestore, firestoreReducer } from 'redux-firestore';
import 'firebase/storage';

const firebaseConfig = {
  apiKey: "AIzaSyCU6F0OXFhfaXKSmDWa32u745lXxqaK-ME",
  authDomain: "bloggy-ebcf1.firebaseapp.com",
  databaseURL: "https://bloggy-ebcf1.firebaseio.com",
  projectId: "bloggy-ebcf1",
  storageBucket: "bloggy-ebcf1.appspot.com",
  messagingSenderId: "942932942625"
};

const rrfConfig = {
  userProfile: 'users',
  useFirestoreForProfile: true,
};

firebase.initializeApp(firebaseConfig);
const firestore = firebase.firestore();
const settings = { timestampsInSnapshots: true };
firestore.settings(settings);
export const storage = firebase.storage();

const createStoreWithFirebase = compose(
  reactReduxFirebase(firebase, rrfConfig),
  reduxFirestore(firebase),
)(createStore);

const rootReducer = combineReducers({
  firebase: firebaseReducer,
  firestore: firestoreReducer,
});

const initialState = {};

const store = createStoreWithFirebase(rootReducer, initialState, compose(reactReduxFirebase(firebase), window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__()));

export default store;