import React, { Component } from 'react';
import Input from '../helpers/Input';
import { Link } from 'react-router-dom';

class Register extends Component {
  constructor(){
    super();
    this.state = {
      email: '',
      password: '',
    }
  }

  onSubmit = e => {
    e.preventDefault();
  }
  onChange = e => {
    this.setState({
      [e.target.name]: e.target.value,
    });
  }

  render(){
    const { email, password } = this.state;
    return (
      <div className="form form__auth form__auth-login">
        <div className="text-center mb-2">
          <h2 className="heading-secondary">Register</h2>
        </div>
        <form onSubmit={this.onSubmit}>
          <Input type="email" id="email" placeholder="Email Address" onChange={this.onChange} label="Email Address" name="email" value={email} />
          <Input type="password" id="password" placeholder="Password" onChange={this.onChange} label="Password" name="password" value={password} />
          <input type="submit" value="Register" className="btn btn--primary btn--auth" />
          <div className="text-center mt-2">
            <Link to="/login" className="form__link">Do you have an account? Sign In</Link>
          </div>
        </form>
      </div>
    );
  }
}

export default Register;