import React, { Component } from 'react';
import { firestoreConnect } from 'react-redux-firebase';
import { compose } from 'redux';
import { connect } from 'react-redux';
import Spinner from '../layouts/Spinner';
import { Link } from 'react-router-dom';

class Single extends Component {
  render(){
    return (
      <React.Fragment>
        {this.props.post ? (
          <div className="sinlge-post text-center">
            <h2 className="single-post__title"><Link className="single-post__link" to={`/post/${this.props.post.id}`}>{this.props.post.title}</Link></h2>
            <div className="single-post__date">
                <small style={{color: '#acafb4', fontSize: '15px', marginRight: '5px'}}>Created At &nbsp;&nbsp;{`${new Date(this.props.post.created_at).getDate()}, ${new Date(this.props.post.created_at).getMonth() + 1}, ${new Date(this.props.post.created_at).getFullYear()}`}</small>
               <ul className="single-post__categories">
                  {this.props.post.categories.map(category => {
                    return (
                      <li className="single-post__category" key={category}>
                        {category}
                      </li>
                    )
                  })}
                </ul>
            </div>
            <div className="single-post__img">
              <img src={this.props.post.url} alt="Thumbnail"/>
            </div>
            <div className="single-post__info">
              <p className="single-post__details">
                {this.props.post.details}
              </p>
            </div>
          </div>
        ) : (
          <div style={{position: 'relative', height: '300px'}}>
            <Spinner />
          </div>
        )}
      </React.Fragment>
    )
  }
}

export default compose(
  firestoreConnect(props => {
    return [
      {collection: 'posts', storeAs: 'post',doc: props.match.params.id},
    ]
  }),
  connect(state => {
    return {
      post: state.firestore.ordered.post && state.firestore.ordered.post[0],
    }
  })
)(Single);