import React, { Component } from 'react';
import $ from 'jquery';
import 'slick-carousel';
import { firestoreConnect } from 'react-redux-firebase';
import { compose } from 'redux';
import { connect } from 'react-redux';
import Spinner from '../layouts/Spinner';
import { Link } from 'react-router-dom';

class Homepage extends Component {
  componentDidUpdate(){
    $('.showcase-slider').not('.slick-initialized').slick({
      dots: false,
      infinite: true,
      speed: 500,
      fade: true,
      cssEase: 'linear',
      autoplay: true,
      arrows: false,
      autoplaySpeed: 6000
    })
  }
  render(){
    const { posts } = this.props;
    return (
      <React.Fragment>
        {posts ? (
          <div className="showcase-slider slider">
            {posts.map(post => {
              return (
              <div className="grid" key={post.id}>
                <figure className="effect-steve">
                  <img src={post.url} alt="img29"/>
                  <figcaption>
                    <h4><span>{post.title}</span></h4>
                    {post.categories.map(category => {
                      return (
                        <p key={category}>{category}</p>
                      );
                    })}
                    <a href="#!">View more</a>
                  </figcaption>			
                </figure>
              </div>
              );
            })}
          </div>
        ) : 
        <div style={{position: 'relative', height: '300px'}}>
          <Spinner />
        </div>}
          {posts ? (
            <React.Fragment>
              <div className="row">
                {posts.map(post => {
                  return (
                    <div className="col-1-3" key={post.id}>
                      <div className="post-card">
                        <Link to={`/post/${post.id}`} className="post-card__imglink">&nbsp;</Link>
                        <div className="post-card__img">
                          <ul className="post-card__categories">
                            {post.categories.map(category => {
                              return (
                                <li className="post-card__category-item" key={category}>
                                  {category}
                                </li>
                              );
                            })}
                          </ul>
                          <img src={post.url} alt="Thumbnail"/>
                        </div>
                        <div className="post-card__info">
                          <h3 className="post-card__title">
                            <Link to={`/post/${post.id}`} className="post-card__link">{post.title}</Link>
                          </h3>
                        </div>
                        <div className="post-card__footer">
                          <span>{`${new Date(post.created_at).getDate()}, ${new Date(post.created_at).getMonth() + 1}, ${new Date(post.created_at).getFullYear()}`}</span>
                        </div>
                      </div>
                    </div>
                  );
                })}
              </div>
            </React.Fragment>
          ) : null}
      </React.Fragment>
    )
  }
}

export default compose(
  firestoreConnect(props => {
    return [
      {collection: 'posts'}
    ]
  }),
  connect(state => {
    return {
      posts: state.firestore.ordered.posts,
    }
  })
)(Homepage);