import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';

class AdminNavbar extends Component {
  render(){
    return (
      <div className="adminnavbar mb-5">
        <div className="text-center">
          <ul className="tabs">
            <li className="tabs__item">
              <NavLink to="/admin/posts" className="tabs__link" exact activeClassName="active">
                <i className="fas fa-list"></i>
                Posts
              </NavLink>
            </li>
            <li className="tabs__item">
              <NavLink to="/admin/posts/add" className="tabs__link" exact activeClassName="active">
                <i className="fas fa-pencil-alt"></i>
                AddPost
              </NavLink>
            </li>
            <li className="tabs__item">
              <NavLink to="/admin/settings" className="tabs__link" exact activeClassName="active">
                <i className="fas fa-cogs"></i>
                Settings
              </NavLink>
            </li>
          </ul>
        </div>
      </div>
    );
  }
}

export default AdminNavbar;