import React, { Component } from 'react';
import AdminNavbar from '../admin/AdminNavbar';
import { storage } from '../../store/store';
import { firestoreConnect } from 'react-redux-firebase';
import { compose } from 'redux';
import { connect } from 'react-redux';

class EditPost extends Component {
  constructor(props){
    super(props);
    this.titleInput = React.createRef();
    this.detailsInput = React.createRef();
    this.state = {
      title: '',
      details: '',
      image: null,
      url: '',
      progress: 0,
      currentCategory: '',
      categories: [],
      wasSubmitted: false,
    }
  }

  static getDerivedStateFromProps(props){
    const { post } = props;
    if(post){
      return {
        title: post.title,
        details: post.details,
        url: post.url,
        categories: post.categories,
      }
    } else {
      return null;
    }
  }

  onChange = e => {
    this.setState({
      [e.target.name]: e.target.value,
    })
  }

  selectFileHandler = e => {
    const image = e.target.files[0];
    if(image.type.split('/')[0] === 'image'){
      this.setState({
        image,
      });
      const uploadTask = storage.ref(`images/${image.name}`).put(image);
      uploadTask.on('state_changed', 
        (snapshot) => {
          const progress = Math.round((snapshot.bytesTransferred / snapshot.totalBytes) * 100) + '%';
          this.setState({
            progress,
          });
        },
        (err) => {
          console.error(Error(err));
        },
        () => {
          storage.ref('images').child(image.name).getDownloadURL().then(url => {
            this.setState({
              url,
            });
          });
        }
      );
    } else {
      // ERROR MESSAGE
    }
  }

  onSubmit = e => {
    e.preventDefault();
    const updPost = {
      title: this.titleInput.current.value,
      details: this.detailsInput.current.value,
    }
    const { firestore, history } = this.props;
    firestore.update({collection: 'posts', doc: this.props.match.params.id}, updPost).then(() => {
      history.push('/admin/posts');
    }).then(err => {
      // ERROR
    })
  }

  render(){
    const { title, details, progress, image, url } = this.state;
    return (
      <React.Fragment>
        <AdminNavbar />
        <div className="container">
          <div className="post-form">
            <div className="text-center my-2">
              <h2 className="heading-secondary">Edit Post</h2>
            </div>
            <div className="form">
              <form onSubmit={this.onSubmit}>
                <div className="form__group">
                  <input type="text" name="title" id="title" className="form__input" defaultValue={title} placeholder="Add Post Title" ref={this.titleInput} />
                  <label htmlFor="title" className="form__label">Add Post Title</label>
                </div>
                <div className="form__group">
                  <input name="details" style={{whiteSpace: 'pre-wrap'}} defaultValue={details}  className="form__input" placeholder="Add Post Details" ref={this.detailsInput} />
                  <label htmlFor="details" className="form__label">Add Post Details</label>
                </div>
                <div className="form__group file-field my-4">
                  <div className="file-field__wrapper">
                    <span>{ image ? image.name : 'Upload Post Thumbnail' }</span>
                    <button className="btn btn--secondary btn-sm file-btn">Choose Image <i className="fas fa-cloud-upload-alt"></i></button>
                  </div>
                  <input type="file" name="thumbnail" id="thumbnail" onChange={this.selectFileHandler} defaultValue={url} />
                  {progress ? (
                    <React.Fragment>
                    <style dangerouslySetInnerHTML={{
                      __html: [
                        '.progress::before{',
                        `content: "${progress}";`,
                        `left: ${parseInt(progress) - 4}%;`,
                        '}',
                      ].join('\n')
                    }}></style>
                    <div className="progress progress--file" style={{width:progress, backgroundColor: progress === '100%' ? '#12d183' : '#05f'}}></div>
                    </React.Fragment>
                  ) : null}
                </div>
                <div className="img-thumbnail">
                  {url !== '' ? (
                    <img src={url} alt="Thumnail"/>
                  ) : null}
                </div>
                <button type="submit" className="btn btn--primary btn-block">Update Post<i className="fas fa-location-arrow"></i></button>
              </form>
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default compose(
  firestoreConnect(props => {
    return [
      {collection: 'posts', storeAs: 'post', doc: props.match.params.id},
    ]
  }),
  connect(state => {
    return {
      post: state.firestore.ordered.post && state.firestore.ordered.post[0],
    }
  })
)(EditPost);