import React, { Component } from 'react';
import AdminNavbar from '../admin/AdminNavbar';
import LoadingScreen from '../layouts/Spinner';
import { firestoreConnect } from 'react-redux-firebase';
import { Link } from 'react-router-dom';
import { compose } from 'redux';
import { connect } from 'react-redux';

class PostsList extends Component {
  deletePost = (id, e) => {
    e.preventDefault();
    const { firestore, history } = this.props;
    firestore.delete({collection: 'posts', doc: id}).then(() => {
      history.push('/admin/posts');
    }).catch(err => {
      // ERROR
    }); 
  }
  render(){
    const { posts } = this.props;
    let markup;
    if(posts){
      if(posts.length > 0){
        markup = 
          <div className="admin-post-wrapper">
            {posts.map((post, i) => {
              return (
                <div className="admin-post" key={post.id}>
                  <div className="admin-post__iterator">
                    <span>{i + 1}</span>
                  </div>
                  <div className="admin-post__info">
                    <h2 className="admin-post__title">{post.title}</h2>
                    <ul className="admin-post__categories">
                      {post.categories.map(category => {
                        return (
                          <li className="admin-post__category" key={category}>{category}</li>
                        )
                      })}
                    </ul>
                  </div>
                  <div className="admin-post__links">
                    <Link to="" className="admin-post__link btn btn--info-outline btn-sm">View Post</Link>
                    <Link to={`/admin/post/edit/${post.id}`} className="admin-post__link btn btn--green-outline btn-sm">Edit Post</Link>
                    <a href="#!" onClick={this.deletePost.bind(this, post.id)} className="admin-post__link btn btn--danger-outline btn-sm">Delete</a>
                  </div>
                </div>
              )
            })}
          </div>
      } else {
        markup =
          <div className="no-posts-wrapper">
            <div className="text-center">
              <h3 className="heading-secondary">Nothing Found</h3>
              <p className="flow-text mb-2">Ready to publish your first post? </p>
              <Link to="/admin/posts/add" className="btn btn--primary">Add Your First Post</Link>
            </div>
          </div>
      }
    } else {
      markup = <LoadingScreen />
    }
    return (
      <React.Fragment>
        <AdminNavbar />
        <div className="container">
          <div className="admin-posts-wrapper">
            {markup}
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default compose(
  firestoreConnect([{collection: 'posts'}]),
  connect(state => {
    return {
      posts: state.firestore.ordered.posts,
    }
  })
)(PostsList);